@import "stylesheets/sdx/images/images";
@import "stylesheets/sdx/link/link";
@import "stylesheets/sdx/typography/typography";

:host {
  padding-top: 0 !important;
  height: auto !important;
  display: inline !important;
}

.component {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: $color-white;
  border-radius: $image-border-radius;
  overflow: hidden; // clip border-radius
  text-decoration: none;
  border: 1px solid transparent;
  box-shadow: 0 4px 20px 0 transparent;
  transition: all 400ms $standard-easing;
  user-select: none;

  &:hover,
  &:focus-visible {
    &.card,
    &.product,
    &.product-out-of-stock,
    &.product-button {
      border-color: $color-gray-tint-4;
      box-shadow: 0 4px 20px 0 rgb($color-gray, 0.16);
    }

    .image {
      transition-duration: 800ms;
      transform: scale(1.05);
    }

    .link--standalone {
      @include standalone-link-hover;
    }
  }

  .first {
    overflow: hidden;

    .image {
      display: block;
      aspect-ratio: 4/3;
      object-fit: cover;
      transition: transform 600ms $standard-easing;
    }
  }

  .second {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: $baseline-2;
    hyphens: auto;
    min-height: 153px;

    @include bp-sm {
      padding: $baseline-3;
    }

    .tag,
    .stock-counter,
    .time-counter,
    .reserved-space {
      height: 5.5 * $baseline;
    }
    .tag {
      margin: -2px 0 2px 0;
    }
    .time-counter {
      padding-top: 6px; // align with stock counter label
    }

    .label {
      margin-bottom: $baseline;
    }

    .description {
      color: $color-gray-tint-2;
      margin-bottom: $baseline-2;
      flex-grow: 1;
    }
  }

  &.split,
  &.split-sidekick {
    background: $color-horizon;
    height: auto;
    flex-direction: column-reverse;

    @include bp-md {
      flex-direction: row-reverse;
    }

    .first,
    .second {
      flex-basis: 50%;
    }
    .second {
      padding: $baseline-3 $baseline-2 $baseline * 5;

      .description {
        color: $color-navy;

        ::slotted(ul) {
          // Icon list
          line-height: $baseline-4;
        }
      }
    }
    .image {
      border-radius: 0;
      object-fit: scale-down;
      height: 100%;
    }
  }

  &.gradient-sidekick {
    @include gradient-2;
    border: 0;

    .first {
      display: none;
      visibility: hidden; // for e2e test
    }
    .label,
    .description {
      color: $color-white;
    }
  }

  &.cover,
  &.cover-sidekick {
    border: 0;

    .first {
      display: none;
      visibility: hidden; // for e2e test
    }
    .second {
      background-position: center;
    }
    .label,
    .description {
      color: $color-white;
      overflow-wrap: break-word;

      ::slotted(ul) {
        line-height: $baseline-4;
      }
      @include bp-sm {
        max-width: min(55%, 340px);
      }
    }
  }

  &.split {
    min-height: 520px;

    @include bp-md {
      min-height: 0;
    }
  }
  &.cover {
    .second {
      min-height: 520px;
    }
  }
  &.split,
  &.cover {
    .second {
      @include bp-md {
        min-height: 480px;
      }
      @include bp-xl {
        min-height: 560px;
      }
    }
  }

  &.split-sidekick,
  &.gradient-sidekick,
  &.cover-sidekick {
    .second {
      min-height: 340px;

      @include bp-md {
        min-height: 432px;
      }
    }
  }
  &.split-sidekick {
    min-height: 340px;

    @include bp-md {
      min-height: 432px;
    }

    .second {
      min-height: 0;
    }
  }

  &.split,
  &.split-sidekick,
  &.gradient-sidekick,
  &.cover,
  &.cover-sidekick {
    .second {
      padding: $baseline-3;

      @include bp-sm {
        padding: $baseline * 6 $baseline-4 $baseline-4;
      }
    }
  }

  &.product,
  &.product-out-of-stock,
  &.product-button {
    .image {
      aspect-ratio: 1/1;
      object-fit: contain;
    }
    .description {
      @include typo-headline-4;
      @include font-semi-bold;
      color: $color-navy;
      margin-bottom: $baseline-3;
    }
    sdx-button {
      min-width: $button-min-width - 4px;
      min-height: $interaction-element-height;
    }

    @include bp-lg {
      .first {
        padding: $baseline-4;
      }
      .second {
        padding: 0 $baseline-4 $baseline * 5;
      }
      .label {
        margin-bottom: 0.5 * $baseline;
      }
      .link--standalone {
        transition: all 400ms $standard-easing;
        opacity: 0;
      }

      &:hover,
      &:focus {
        .link--standalone {
          opacity: 1;
        }
      }
    }

    // mobile and tablet
    @include bp-md-max {
      border-radius: 0;

      .image {
        transform: scale(1);
      }
      .label,
      .description {
        @include typo-headline-5;
        @include font-semi-bold;
      }
      .second {
        padding: $baseline-2 0 0;
      }

      &:hover,
      &:focus {
        border-color: transparent;
        box-shadow: none;
      }
    }

    &.product-out-of-stock {
      .image {
        opacity: 0.5;
      }
    }
  }
}

.component.sdx--dark-theme:not(.gradient) {
  background-color: $color-dusk-tint-3-dark-theme;

  @include bp-md-max {
    &.product,
    &.product-out-of-stock,
    &.product-button {
      background-color: transparent;
    }
  }

  &:hover,
  &:focus-visible {
    &.card {
      border-color: $color-gray-tint-4-dark-theme;
      box-shadow: 0 4px 20px 0 rgb($color-gray-dark-theme, 0.16);
    }

    .link--standalone {
      @include standalone-link-hover-dark-theme;
    }
  }

  .second {
    .label {
      color: $color-gray-dark-theme; // Override teaser link color
    }
    .description {
      color: $color-gray-tint-2-dark-theme;
    }
  }
}
