import { Component, Element, h, Prop } from "@stencil/core"
import {
  getAppearance,
  translate,
} from "../../core/helpers/webcomponent-helpers"

@Component({
  tag: "sdx-teaser",
  styleUrl: "teaser.scss",
  shadow: true,
})
export class Teaser {
  @Element() public el!: HTMLSdxTeaserElement

  /**
   * The layout type defines the appearance of the teaser.
   */
  @Prop({ reflect: true }) public layout:
    | "card"
    | "split"
    | "split-sidekick"
    | "cover"
    | "cover-sidekick"
    | "gradient-sidekick"
    | "product"
    | "product-out-of-stock"
    | "product-button" = "card"

  /**
   * The colour variation of tags and counters.
   */
  @Prop() public theme?: "standard" | "sales"

  /**
   * Title that will be displayed in the teaser's header.
   */
  @Prop() public label: string = " "

  /**
   * What aria level the label in the header relates to for accessibility.
   */
  @Prop() public labelAriaLevel?: number

  /**
   * Optional tag label.
   */
  @Prop() public tagLabel?: HTMLSdxTagElement["label"]

  /**
   * Optional tag theme. Requires a `tag-label`.
   * @deprecated use theme instead.
   */
  @Prop() public tagTheme: HTMLSdxTagElement["theme"] = "standard"

  /**
   * Optional tag icon. Requires a `tag-label`.
   */
  @Prop() public tagIconName: HTMLSdxTagElement["iconName"]

  /**
   * Optional button icon. Useful for `product-button` layouts.
   */
  @Prop() public buttonIconName: HTMLSdxButtonElement["iconName"]

  /**
   * Optional date for time counter, useful for product layouts.
   */
  @Prop() public date: HTMLSdxTimeCounterElement["date"]

  /**
   * Optional amount for stock counter, useful for product layouts.
   */
  @Prop() public stockAmount: HTMLSdxStockCounterElement["amount"] = 0

  /**
   * Optional total for stock counter, useful for product layouts.
   */
  @Prop() public stockTotal: HTMLSdxStockCounterElement["total"] = 0

  /**
   * The image to display on the teaser (also supports SVG).
   */
  @Prop() public imageSrc: string =
    "data:image/gif;base64,R0lGODlhAQABAAD/ACwAAAAAAQABAAACADs="

  /**
   * An alternate text for the image if it can not be displayed and for screen readers.
   */
  @Prop() public imageAlt: string = ""

  /**
   * How the image fits inside (reflects the CSS "background-size" property). Only for `cover`/`cover-sidekick` teasers.
   */
  @Prop() public backgroundSize: string = "cover"

  /**
   * Renders a standalone or anchor link at the bottom of the teaser. Needs the `href-label` attribute.
   */
  @Prop() public href?: string

  /**
   * Renders a standalone or anchor link at the bottom of the teaser. Needs the `href` attribute.
   */
  @Prop() public hrefLabel?: string = " "

  /**
   * Adds a meaningful description to the link for screen readers.
   */
  @Prop() public hrefAriaLabel?: string

  private hasDarkBackground() {
    return this.layout.startsWith("gradient") || this.layout.startsWith("cover")
  }

  private getHeading() {
    let h = "h3"
    if (this.layout.startsWith("cover")) {
      h = "h2"
    }
    if (this.layout.startsWith("product")) {
      h = "h4"
    }
    return h
  }

  private getComponentClassNames() {
    return {
      component: true,
      [this.layout]: true,
      [getAppearance(this.el)]: true,
    }
  }

  private getLinkClassNames() {
    return {
      [this.href?.startsWith("#") ? "link--anchor" : "link--standalone"]: true,
      "link--white": this.hasDarkBackground(),
    }
  }

  private getLabelClassNames() {
    return {
      label: true,
      [this.getHeading()]: true,
    }
  }

  private getBackgroundStyles() {
    if (!this.layout.startsWith("cover")) {
      return
    }
    const gradient = "linear-gradient(90deg, #1d1d1d 0%, rgb(29,29,29, 0) 100%)" // $color-dusk-tint-2-dark-theme
    return {
      backgroundImage: `${gradient}, url(${this.imageSrc})`,
      backgroundSize: this.backgroundSize,
    }
  }

  public render() {
    return (
      <a
        class={this.getComponentClassNames()}
        href={this.href}
        draggable={false}
      >
        <div class="first">
          <img
            class="image image--rounded"
            src={this.imageSrc}
            alt={this.imageAlt}
            draggable={false}
          />
        </div>
        <div class="second" style={this.getBackgroundStyles()}>
          {!this.date && !this.stockAmount && this.tagLabel && (
            <sdx-tag
              class="tag"
              label={this.tagLabel}
              theme={
                this.hasDarkBackground()
                  ? "standard"
                  : this.theme || this.tagTheme
              }
              icon-name={this.tagIconName}
              background={this.hasDarkBackground() ? "dark" : "light"}
            />
          )}
          {this.date && (
            <sdx-time-counter
              class="time-counter"
              theme={this.theme}
              date={this.date}
            />
          )}
          {!!this.stockAmount && (
            <sdx-stock-counter
              class="stock-counter"
              theme={this.theme}
              amount={this.stockAmount}
              total={this.stockTotal}
            />
          )}
          {this.layout.startsWith("product") &&
            !this.date &&
            !this.stockAmount &&
            !this.tagLabel && <div class="reserved-space" />}
          <div
            class={this.getLabelClassNames()}
            role={this.labelAriaLevel ? "heading" : undefined}
            aria-level={this.labelAriaLevel}
          >
            {this.label}
          </div>
          <div class="description">
            <slot />
          </div>
          {this.layout === "product-button" && (
            <sdx-button
              theme="secondary"
              icon-name={this.buttonIconName}
              label={this.hrefLabel || translate("Add to cart", this.el.lang)}
            />
          )}
          {this.layout !== "product-button" && (
            <a
              href={this.href}
              class={this.getLinkClassNames()}
              aria-label={this.hrefAriaLabel}
            >
              {this.hrefLabel}
            </a>
          )}
        </div>
      </a>
    )
  }
}
